.coaches__container {
  padding-top: 112px;
  padding-bottom: 105px;

  @include vp-1199 {
    padding-top: 105px;
    padding-bottom: 101px;
  }

  @include vp-767 {
    padding-top: 67px;
    padding-bottom: 70px;
  }
}

.coaches__title {
  margin: 0;
  margin-bottom: 53px;

  @include vp-1199 {
    margin-bottom: 35px;
    text-align: center;
  }

  @include vp-767 {
    margin-bottom: 45px;
  }
}

.coaches__slider {
  position: relative;

  @include vp-1199 {
    padding: 0 56px;
  }

  @include vp-767 {
    padding: 0 32px;
  }
}

.coaches__button-prev {
  position: absolute;
  left: -70px;
  top: 50%;
  transform: translate(0, -50%);

  @include vp-1199 {
    left: -10px;
  }
}

.coaches__slide {
  position: relative;
}

.coaches__button-next {
  position: absolute;
  right: -70px;
  top: 50%;
  transform: translate(0, -50%);

  @include vp-1199 {
    right: -10px;
  }
}
